.modal-overlay{position:fixed;top:0;left:0;width:100vw;height:100vh;background:rgba(0,0,0,.767);display:flex;justify-content:center;align-items:center;z-index:3}.modal-overlay .modal-content{background:#fff;padding:2rem;border-radius:12px;position:relative;max-width:90%;max-height:90%;overflow-y:auto;z-index:4}.modal-overlay .modal-content .close-button{color:gray;position:absolute;top:1rem;right:1rem;border:none;background:rgba(0,0,0,0);font-size:1.5rem;z-index:5;cursor:pointer;transition:color ease .3s}.modal-overlay .modal-content .close-button:hover{color:red}